export default "<span class=\"hljs-keyword\">import</span> { Component, ChangeDetectionStrategy } from <span class=\"hljs-string\">'@angular/core'</span>;\n<span class=\"hljs-keyword\">import</span> { CalendarEvent, CalendarMonthViewDay } from <span class=\"hljs-string\">'angular-calendar'</span>;\n<span class=\"hljs-keyword\">import</span> { colors } from <span class=\"hljs-string\">'../demo-utils/colors'</span>;\n\n@Component({\n  selector: <span class=\"hljs-string\">'mwl-demo-component'</span>,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n  templateUrl: <span class=\"hljs-string\">'template.html'</span>,\n})\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">class</span> DemoComponent {\n  viewDate: <span class=\"hljs-built_in\">Date</span> = <span class=\"hljs-keyword\">new</span> <span class=\"hljs-built_in\">Date</span>();\n\n  events: CalendarEvent&lt;{ incrementsBadgeTotal: <span class=\"hljs-built_in\">boolean</span> }&gt;[] = [\n    {\n      title: <span class=\"hljs-string\">'Increments badge total on the day cell'</span>,\n      color: colors.yellow,\n      start: <span class=\"hljs-keyword\">new</span> <span class=\"hljs-built_in\">Date</span>(),\n      meta: {\n        incrementsBadgeTotal: <span class=\"hljs-literal\">true</span>,\n      },\n    },\n    {\n      title: <span class=\"hljs-string\">'Does not increment the badge total on the day cell'</span>,\n      color: colors.blue,\n      start: <span class=\"hljs-keyword\">new</span> <span class=\"hljs-built_in\">Date</span>(),\n      meta: {\n        incrementsBadgeTotal: <span class=\"hljs-literal\">false</span>,\n      },\n    },\n  ];\n\n  beforeMonthViewRender({ body }: { body: CalendarMonthViewDay[] }): <span class=\"hljs-built_in\">void</span> {\n    body.forEach((day) =&gt; {\n      day.badgeTotal = day.events.filter(\n        (event) =&gt; event.meta.incrementsBadgeTotal\n      ).length;\n    });\n  }\n}\n";